'use client';

import { Plus } from 'lucide-react';
import { Button } from '@/components/ui/button';

// 1. Import Custom Hook
import { usePaymentMethodPage } from '@/hooks/admin/usePaymentMethodPage';

// 2. Import Komponen UI
import PaymentMethodFilters from '@/components/admin/payment-method/PaymentMethodFilters';
import PaymentMethodTable from '@/components/admin/payment-method/PaymentMethodTable';
import PaymentMethodFormDialog from '@/components/admin/payment-method/PaymentMethodFormDialog';
import { PaymentMethod } from '@/types';

export default function PaymentMethodPage() {
    // 3. Panggil Logika dari Hook
    const {
        // Data
        methods,
        totalData,
        loading,
        
        // Filter & Pagination
        currentPage,
        itemsPerPage,
        searchQuery,
        setSearchQuery,
        setCurrentPage,
        
        // State Modal & Form
        isDialogOpen,
        setIsDialogOpen,
        isSaving,
        editId, 
        formData,
        setFormData,

        // Handlers
        handleOpenDialog,
        handleFileChange,
        handleSubmit,
        handleDelete
    } = usePaymentMethodPage();

    return (
        <div className="space-y-6">
            {/* --- Header Section --- */}
            <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
                <div>
                    <h1 className="text-2xl font-bold text-gray-900">Metode Pembayaran</h1>
                    <p className="text-gray-500 text-sm">Atur opsi pembayaran (Cash, Transfer, QRIS).</p>
                </div>
                
                <Button 
                    className="bg-[#D93F21] hover:bg-[#b9351b] shadow-sm" 
                    onClick={() => handleOpenDialog()}
                >
                    <Plus className="w-4 h-4 mr-2" /> Tambah Metode
                </Button>
            </div>

            {/* --- Filter --- */}
            <PaymentMethodFilters 
                searchQuery={searchQuery}
                setSearchQuery={setSearchQuery}
            />

            {/* --- Tabel Data --- */}
            <PaymentMethodTable 
                loading={loading}
                methods={methods}
                totalData={totalData}
                currentPage={currentPage}
                itemsPerPage={itemsPerPage}
                onPageChange={setCurrentPage}
                onEdit={handleOpenDialog}
                onDelete={handleDelete}
            />

            {/* --- Modal Form --- */}
            <PaymentMethodFormDialog 
                isOpen={isDialogOpen}
                onOpenChange={setIsDialogOpen}
                onSubmit={handleSubmit}
                isSaving={isSaving}
                formData={formData}
                setFormData={setFormData}
                // initialData digunakan untuk cek mode Edit/Create di dalam dialog
                // Casting ke any untuk menghindari error tipe pada properti icon (File vs string)
                initialData={editId ? { id: editId, ...formData } as any : null} 
                onFileChange={handleFileChange}
            />
        </div>
    );
}