'use client';

import { FormEvent, ChangeEvent } from 'react';
import { Loader2, UploadCloud } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Switch } from '@/components/ui/switch';
import { Textarea } from '@/components/ui/textarea';
import {
    Dialog, DialogContent, DialogFooter, DialogHeader, DialogTitle, DialogDescription,
} from '@/components/ui/dialog';
import { PaymentMethod } from '@/types';

interface PaymentMethodFormDialogProps {
    isOpen: boolean;
    onOpenChange: (open: boolean) => void;
    onSubmit: () => void;
    // Tambahkan properti isSaving di sini
    isSaving: boolean;
    formData: {
        nama_metode: string;
        keterangan: string;
        is_aktif: boolean;
        icon: File | null;
    };
    setFormData: (data: any) => void;
    initialData: PaymentMethod | null;
    onFileChange: (e: ChangeEvent<HTMLInputElement>) => void;
}

export default function PaymentMethodFormDialog({
    isOpen,
    onOpenChange,
    onSubmit,
    isSaving, // Ambil props isSaving
    formData,
    setFormData,
    initialData,
    onFileChange
}: PaymentMethodFormDialogProps) {
    
    const handleSubmit = (e: FormEvent) => {
        e.preventDefault();
        onSubmit();
    };

    return (
        <Dialog open={isOpen} onOpenChange={onOpenChange}>
            <DialogContent className="sm:max-w-[500px]">
                <DialogHeader>
                    <DialogTitle>{initialData ? 'Edit Metode' : 'Tambah Metode Baru'}</DialogTitle>
                    <DialogDescription>
                        {initialData ? 'Perbarui informasi metode pembayaran.' : 'Masukkan detail metode pembayaran baru.'}
                    </DialogDescription>
                </DialogHeader>

                <form onSubmit={handleSubmit} className="space-y-5 mt-2">
                    <div className="space-y-2">
                        <Label htmlFor="nama">Nama Metode <span className="text-red-500">*</span></Label>
                        <Input 
                            id="nama" 
                            placeholder="Contoh: Transfer BCA" 
                            value={formData.nama_metode}
                            // Menggunakan (prev: any) untuk menghindari error implisit any
                            onChange={(e) => setFormData((prev: any) => ({...prev, nama_metode: e.target.value}))}
                            required
                        />
                    </div>

                    <div className="space-y-2">
                        <Label htmlFor="status">Status Aktif</Label>
                        <div className="flex items-center space-x-2 h-10 border rounded-md px-3">
                            <Switch 
                                id="status"
                                checked={formData.is_aktif} 
                                onCheckedChange={(c) => setFormData((prev: any) => ({...prev, is_aktif: c}))} 
                            />
                            <span className="text-sm text-gray-500">
                                {formData.is_aktif ? 'Aktif (Dapat digunakan)' : 'Non-Aktif (Disembunyikan)'}
                            </span>
                        </div>
                    </div>
                    
                    <div className="space-y-2">
                        <Label htmlFor="keterangan">Keterangan / No. Rekening</Label>
                        <Textarea 
                            id="keterangan" 
                            placeholder="Contoh: No. Rek 1234567890 a.n PT MainDiHatta" 
                            value={formData.keterangan}
                            onChange={(e) => setFormData((prev: any) => ({...prev, keterangan: e.target.value}))}
                            className="resize-none h-24"
                        />
                    </div>
                    
                    <div className="space-y-2">
                        <Label htmlFor="icon">Logo Bank / E-Wallet</Label>
                        <div className="border-2 border-dashed border-gray-200 rounded-lg p-4 hover:bg-gray-50 transition-colors text-center cursor-pointer relative">
                            <input 
                                id="icon" 
                                type="file" 
                                accept="image/*"
                                onChange={onFileChange}
                                className="absolute inset-0 w-full h-full opacity-0 cursor-pointer"
                            />
                            <div className="flex flex-col items-center justify-center gap-1 text-sm text-gray-500">
                                <UploadCloud className="w-8 h-8 text-gray-400 mb-1" />
                                <span className="font-medium text-gray-700">Klik untuk upload logo</span>
                                <span className="text-xs text-gray-400">JPG, PNG (Max 2MB)</span>
                                {formData.icon && (
                                    <span className="text-xs text-green-600 font-bold mt-1 bg-green-50 px-2 py-0.5 rounded">
                                        File terpilih: {formData.icon.name}
                                    </span>
                                )}
                            </div>
                        </div>
                    </div>

                    <DialogFooter className="pt-2">
                        <Button type="button" variant="outline" onClick={() => onOpenChange(false)}>
                            Batal
                        </Button>
                        <Button type="submit" disabled={isSaving} className="bg-[#D93F21] hover:bg-[#b9351b]">
                            {isSaving && <Loader2 className="mr-2 h-4 w-4 animate-spin" />} 
                            {initialData ? 'Perbarui' : 'Simpan'}
                        </Button>
                    </DialogFooter>
                </form>
            </DialogContent>
        </Dialog>
    );
}