'use client';

import { useState, useEffect, ChangeEvent } from 'react';
import axios from '@/lib/axios';
import { toast } from 'sonner';
import { PaymentMethod } from '@/types';

export function usePaymentMethodPage() {
    // State Data
    const [methods, setMethods] = useState<PaymentMethod[]>([]);
    const [loading, setLoading] = useState(true);
    
    // Filter & Pagination
    const [searchQuery, setSearchQuery] = useState('');
    const [currentPage, setCurrentPage] = useState(1);
    const [itemsPerPage] = useState(10);

    // Modal & Form State
    const [isDialogOpen, setIsDialogOpen] = useState(false);
    const [isSaving, setIsSaving] = useState(false);
    const [editId, setEditId] = useState<number | null>(null);
    const [formData, setFormData] = useState({
        nama_metode: '',
        keterangan: '',
        is_aktif: true,
        icon: null as File | null,
    });

    // 1. Fetch Data
    const fetchMethods = async () => {
        setLoading(true);
        try {
            const response = await axios.get('/api/paymentMethods');
            setMethods(response.data);
        } catch (error) {
            console.error("Error fetching data:", error);
            toast.error("Gagal memuat data metode pembayaran.");
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => {
        fetchMethods();
    }, []);

    // 2. Filter Logic
    const filteredMethods = methods.filter((item) => 
        item.nama_metode.toLowerCase().includes(searchQuery.toLowerCase())
    );

    // Pagination Logic
    useEffect(() => { setCurrentPage(1); }, [searchQuery]);
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = filteredMethods.slice(indexOfFirstItem, indexOfLastItem);

    // 3. Handlers
    const resetForm = () => {
        setFormData({ nama_metode: '', keterangan: '', is_aktif: true, icon: null });
        setEditId(null);
        setIsDialogOpen(false);
    };

    const handleOpenDialog = (item?: PaymentMethod) => {
        if (item) {
            setEditId(item.id);
            setFormData({
                nama_metode: item.nama_metode,
                keterangan: item.keterangan || '',
                is_aktif: Boolean(item.is_aktif),
                icon: null
            });
        } else {
            resetForm();
        }
        setIsDialogOpen(true);
    };

    const handleFileChange = (e: ChangeEvent<HTMLInputElement>) => {
        if (e.target.files && e.target.files[0]) {
            setFormData(prev => ({ ...prev, icon: e.target.files![0] }));
        }
    };

    const handleSubmit = async () => {
        setIsSaving(true);
        try {
            if (editId) {
                // UPDATE
                await axios.put(`/api/paymentMethods/${editId}`, {
                    nama_metode: formData.nama_metode,
                    keterangan: formData.keterangan,
                    is_aktif: formData.is_aktif
                });
                
                if (formData.icon) {
                    const imgData = new FormData();
                    imgData.append('icon', formData.icon);
                    await axios.post(`/api/paymentMethods/${editId}/icon`, imgData, {
                        headers: { 'Content-Type': 'multipart/form-data' },
                    });
                }
                toast.success("Metode berhasil diperbarui!");
            } else {
                // CREATE
                const data = new FormData();
                data.append('nama_metode', formData.nama_metode);
                data.append('keterangan', formData.keterangan);
                data.append('is_aktif', formData.is_aktif ? '1' : '0');
                if (formData.icon) {
                    data.append('icon', formData.icon);
                }

                await axios.post('/api/paymentMethods', data, {
                    headers: { 'Content-Type': 'multipart/form-data' },
                });
                toast.success("Metode berhasil ditambahkan!");
            }
            
            setIsDialogOpen(false);
            resetForm();
            fetchMethods();
            
        } catch (error: any) {
            console.error(error);
            const message = error.response?.data?.message || "Gagal menyimpan data.";
            toast.error(message);
        } finally {
            setIsSaving(false);
        }
    };

    const handleDelete = async (id: number) => {
        if(!confirm("Yakin hapus metode ini?")) return;
        try {
            await axios.delete(`/api/paymentMethods/${id}`);
            toast.success("Dihapus!");
            fetchMethods();
        } catch (error) {
            toast.error("Gagal menghapus.");
        }
    };

    return {
        // Data & State
        methods: currentItems,
        totalData: filteredMethods.length,
        loading,
        currentPage,
        itemsPerPage,
        searchQuery,
        
        // Modal & Form State
        isDialogOpen,
        isSaving,
        editData: editId ? { id: editId, ...formData } : null, // Helper untuk form
        editId, // Return ID untuk pengecekan isEdit
        formData,

        // Setters
        setSearchQuery,
        setCurrentPage,
        setFormData,
        setIsDialogOpen,

        // Functions
        handleOpenDialog,
        handleFileChange,
        handleSubmit,
        handleDelete,
        resetForm
    };
}